.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.heading {
  background-color: gray;
  height: 150px;
  width: 100%;
  text-align: center;
}

.passwords {
  height: 60px;
  width: 100%;
  margin-top: 30px;
  background-color: lightgray;
  display: flex;
  justify-content: space-between;
}

.child1, .child2 {
  flex-basis: 50%;
}
.content {
  margin: 0 auto;
  width: 800px;
}

.errorMessage {
  margin-top: 30px;
  height: 400px;
  display: flex;
  justify-content: center;
  background-color: #eee;
}

.errorMessageContent {
  align-self: center;
  background-color: #ccc;
  display: inline;
}

.mainArea {
  margin-top: 30px;
  height: 400px;
  display: flex;
  justify-content: space-between;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
